import {React,useState} from 'react'
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Image from 'react-bootstrap/Image';
import Logo from '../../assets/images/LogoWebPI.png'
import {Link} from 'react-router-dom'
// import {HashLink} from 'react-router-hash-link'
import ScrollToTop from '../../Utils/ScrollToTop'
import './Navbar.css'

const NavbarComp = () => {
  

  const [isScrolled,setIsScrolled] = useState(false);
  const [currentWidth,setCurrentWidth] = useState(window.innerWidth);

  const navMenus = [
    {
      path:"/",
      menu:"Home"
    },
    {
      path:"/about-us",
      menu:"About Us"
    },
    {
      path:"/services",
      menu:"Services"
    },
    {
      path:"/our-team",
      menu:"Our Team"
    },
    {
      path:"/contact-us",
      menu:"Contact Us"
    },
  ]

  window.addEventListener('scroll',(event)=>{
    if(window.scrollY === 0){
      setIsScrolled(false);
    }else if(window.scrollY > 0){
      setIsScrolled(true);
    }
  });

  window.addEventListener('resize',()=>{
    setCurrentWidth(window.innerWidth);
  })



  return (
    <Navbar collapseOnSelect expand="lg" className={`fixed-top ${currentWidth > 991 ? (isScrolled ? 'bg-light navbar-light shadow' :'bg-transparent navbar-dark'):'bg-light navbar-light shadow'}`} id="navBarContainer" style={{zIndex:10000}}>
      <Container>
        <Navbar.Brand as={Link} to="/" className={`fw-bold fs-3 ${currentWidth > 991 && (!isScrolled && 'text-light')} `} id="brandName">
          <Image src={Logo} />
           WebPI
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ms-auto" id="navLinks" collapseonselect={`false`}>
            {
              navMenus.map((menu,index)=>{
                return(
                  <Nav.Link eventKey={`${index}`} as={Link} to={menu.path} className={`fw-normal fs-6 ms-md-3 text-uppercase ${currentWidth > 991 ? (isScrolled ? 'text-dark':'text-light'):'text-dark'}`} key={index} onClick={ScrollToTop} >{menu.menu}</Nav.Link>
                )
              })
            }
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}

export default NavbarComp