import React from 'react'
import { Form, Button } from 'react-bootstrap';

const ContactForm = (props) => {
    return (
        <div>
            <Form className={`p-2 rounded mb-3 ${props.FormStyle}`}>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                    <Form.Label>Name</Form.Label>
                    <Form.Control className="bg-transparent border border-dark" type="text" placeholder="Your Name" />
                </Form.Group>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                    <Form.Label>Email</Form.Label>
                    <Form.Control className="bg-transparent border border-dark" type="email" placeholder="name@example.com" />
                </Form.Group>
                <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                    <Form.Label>Message</Form.Label>
                    <Form.Control className="bg-transparent border border-dark" as="textarea" rows={3} placeholder="Your message..." />
                </Form.Group>
                <div className="text-center">
                    <Button variant="primary" type="submit">Send Message</Button>
                </div>
            </Form>
        </div>
    )
}

export default ContactForm