import React from 'react'
import { Link } from 'react-router-dom'
import { HiOutlineMail, HiOutlinePhoneIncoming } from "react-icons/hi";
import ContactForm from '../ContactForm/ContactForm';
import ScrollToTop from '../../Utils/ScrollToTop'

import './Footer.css'

const Footer = () => {
    const footerLinks = [
        {
            name: "Home",
            path: '/'
        },
        {
            name: "Services",
            path: '/services'
        },
        {
            name: "Team",
            path: '/our-team'
        },
        {
            name: "Contact Us",
            path: '/contact-us'
        },
        {
            name: "About Us",
            path: '/about-us'
        },
        {
            name: "Privacy & Policy",
            path: '/privacy-policy'
        },
        {
            name: "Terms & Conditions",
            path: '/terms-conditions'
        },
    ]
    return (
        <div className="container-fluid py-5 footer-box">
            <div className="container">
                <div className="row justify-content-between">
                    <div className="col-md-5 col-10 footer-inner-box ">
                        <h2 className="mb-0">Leave a message</h2>
                        <div className="bar"></div>
                        <ContactForm />
                    </div>
                    <div className="col-md-3 col-10 footer-inner-box">
                        <h2 className="mb-0">Useful Links</h2>
                        <div className="bar"></div>
                        <ul className='mt-2'>
                            {
                                footerLinks.map((item, index) => (
                                    <li key={index}><Link to={item.path} onClick={ScrollToTop} >{item.name}</Link></li>
                                ))
                            }
                        </ul>
                    </div>
                    <div className="col-md-3 col-10 footer-inner-box ">
                        <h2 className="mb-0">Contact Us</h2>
                        <div className="bar"></div>
                        <p className="mt-2 mb-1">
                            For any bussiness purposes, contact us at<br />
                        </p>

                        <span className="">
                            <HiOutlineMail className="fs-4 mb-2" /> info@webpi.in
                        </span>
                        <br />
                        <span className="">
                            <HiOutlinePhoneIncoming className="fs-4" /> +918789747456
                        </span>
                    </div>
                </div>
                <div className=" text-center">
                    <hr />
                    <span className="fw-bold copyright">Copyright &copy; {new Date().getFullYear()} <Link to="/">WebPI</Link>. <span>All rights reserved!</span> </span>
                </div>
            </div>
        </div>
    )
}

export default Footer