import React from 'react'
import './Client.css'

// client's logo
import client1 from '../../assets/images/client-1.png'
import client2 from '../../assets/images/client-2.png'
import client3 from '../../assets/images/client-3.png'
import client4 from '../../assets/images/client-4.png'
import client5 from '../../assets/images/client-5.png'
import client6 from '../../assets/images/client-6.png'

const ClientComp = () => {
    const clientList = [
        {
            logo: client1,
        },
        {
            logo: client2,
        },
        {
            logo: client3,
        },
        {
            logo: client4,
        },
        {
            logo: client5,
        },
        {
            logo: client6,
        },
    ]


    return (
        <div className='container-fluid py-5 client-box'>
            <div className="container client-inner-box">
                <h2 className='text-center'>Our Clients</h2>
                <div className="d-flex justify-content-center pb-3">
                    <div style={{ height: "3px", width: "8%", backgroundColor: "rgba(25,25,25,1)" }}></div>
                </div>
                <div className="row justify-content-center">
                    {
                        clientList.map((client, index) => (
                            <div className="col-lg-2 col-md-4 col-6 py-2 client-list" key={index}>
                                <img className="img-fluid" src={client.logo} alt={`client${index+1}`} />
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>
    )
}

export default ClientComp