import React from 'react'
import './Contact.css'
import { HiOutlineMail, HiOutlinePhoneIncoming } from "react-icons/hi";
import ContactForm from '../ContactForm/ContactForm';

const Contact = () => {
    return (
        <div className="container-fluid p-0 contact">
            <div className="container py-3">
                <h2 className="heading text-center pt-md-4 pt-2">Contact Us</h2>
                <div className="d-flex justify-content-center pb-2 pb-md-4">
                    <div style={{ height: "3px", width: "8%", backgroundColor: "rgba(25,25,25,1)" }}></div>
                </div>
                <div className="row justify-content-center align-items-center">
                    <div className="col-md-7 col-12">
                        <ContactForm FormStyle="border card bg-light p-4" />
                    </div>
                    <div className="col-md-4 col-12">
                        <div className="card my-2 p-3 contact-info">
                            <span><HiOutlineMail className="fs-4 mb-2" /><span className="fs-4"> Our Email</span></span>
                            <div className="ps-4">info@webpi.in</div>
                        </div>
                        <div className="card my-2 p-2 contact-info">
                            <span><HiOutlinePhoneIncoming  className="fs-4 mb-2" /><span className="fs-4"> Our Phone</span></span>
                            <div className="ps-4">+918789747456</div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    )
}

export default Contact