import React from 'react'
import './Service.css'

// Icons
import { IoMdDesktop, IoMdGlobe } from "react-icons/io";
const ServiceComp = (props) => {

    const services = [
        {
            id: "web-dev",
            icon: <IoMdDesktop className="icon" />,
            title: "Web Development",
            description: "Ignite your online presence with our cutting-edge web development solutions. Our expert team crafts visually stunning and user-friendly websites that captivate your audience and drive engagement"
        },
        {
            id: "digital-marketing",
            icon: <IoMdGlobe className="icon" />,
            title: "Digital Marketing",
            description: "Propel your business to new heights with our results-driven digital marketing strategies. From SEO to PPC, we optimize your online visibility, attract targeted traffic, and convert leads into loyal customers."
        },
        {
            id: "graphics-design",
            icon: <IoMdDesktop className="icon" />,
            title: "Graphic Designing",
            description: "Elevate your brand identity with our creative graphic designs. From logos to brand collaterals, we craft visually stunning assets that communicate your brand message effectively."
        },
        {
            id: "web-dev",
            icon: <IoMdDesktop className="icon" />,
            title: "Social Media Marketing",
            description: "Harness the power of social media to connect, engage, and grow your brand. Our social media experts create compelling campaigns that resonate with your audience and fuel business growth."
        },
        {
            id: "web-dev",
            icon: <IoMdDesktop className="icon" />,
            title: "Video Editing",
            description: "Tell your story through captivating visuals with our professional video editing services. We bring your ideas to life, creating impactful videos that leave a lasting impression on your viewers."
        },
        {
            id: "web-dev",
            icon: <IoMdDesktop className="icon" />,
            title: "App Development",
            description: "Ignite your online presence with our app development solutions. Our expert team crafts visually stunning and smooth apps that captivate your audience and drive engagement"
        }
    ]

    return (
        <div className="service py-md-4 py-2" id="services">
            <div className="container">
                <div className="header text-center py-md-4 py-2">
                    <h2>{props.heading}</h2>
                    <div className="d-flex justify-content-center pb-4">
                        <div style={{ height: "3px", width: "8%", backgroundColor: "rgba(25,25,25,1)" }}></div>
                    </div>
                    <p className="service-description">
                        Welcome to the gateway of digital success! Our expert team at WebPI
                        empowers your brand with cutting-edge digital marketing solutions. Amplify
                        your online presence and unlock new horizons of growth. Drive your sales to
                        unprecedented heights through our tailored strategies that engage, inspire,
                        and convert. From crafting compelling content to optimizing SEO, we are your
                        one-stop-shop for dominating the digital landscape. Stay ahead of the
                        competition with our data-driven approach and innovative techniques. Let us
                        be your partners in progress and take your business to the zenith of the digital
                        world. Embrace the future of success with WebPI!

                    </p>
                </div>
                <div className="row align-items-center justify-content-around">
                    {
                        services.map((service, index) => (
                            <div className="col-md-6 col-lg-4" key={index} >
                                <div className="box" style={{ height: "19rem" }}>
                                    <div className="icon-container" id={service.id}>
                                        {service.icon}
                                    </div>
                                    <div className="title">{service.title}</div>
                                    <div className="description pb-2">
                                        <p>{service.description}</p>
                                    </div>
                                </div>
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>
    )
}

export default ServiceComp