import React from 'react'
import './Team.css'
import profileImg from '../../assets/images/Chandan.jpg'
import profileImg1 from '../../assets/images/Adhrit.jpg'
import profileImg2 from '../../assets/images/Mithlesh.jpg'
import profileImg3 from '../../assets/images/Navneet.jpg'

const TeamComp = () => {
    const teamDetails = [
        {
            profileImg: profileImg,
            name: "Chandan Kumar",
            Role: "Founder & CEO",
            socialHandel: [
                {
                    name: "LinkedIn",
                    icon: "",
                    link: ""
                },
                {
                    name: "Instagram",
                    icon: "",
                    link: ""
                },
                {
                    name: "Facebook",
                    icon: "",
                    link: ""
                }
            ]
        },
        {
            profileImg: profileImg1,
            name: "Adhrit Verma",
            Role: "CTO & Full Stack Developer",
            socialHandel: [
                {
                    name: "LinkedIn",
                    icon: "",
                    link: ""
                },
                {
                    name: "Instagram",
                    icon: "",
                    link: ""
                },
                {
                    name: "Facebook",
                    icon: "",
                    link: ""
                }
            ]
        },
        {
            profileImg: profileImg2,
            name: "Mithlesh Sah",
            Role: "Project Manager & Marketing Head",
            socialHandel: [
                {
                    name: "LinkedIn",
                    icon: "",
                    link: ""
                },
                {
                    name: "Instagram",
                    icon: "",
                    link: ""
                },
                {
                    name: "Facebook",
                    icon: "",
                    link: ""
                }
            ]
        },
        {
            profileImg: profileImg3,
            name: "Navneet Prakash",
            Role: "Graphic Designer",
            socialHandel: [
                {
                    name: "LinkedIn",
                    icon: "",
                    link: ""
                },
                {
                    name: "Instagram",
                    icon: "",
                    link: ""
                },
                {
                    name: "Facebook",
                    icon: "",
                    link: ""
                }
            ]
        },
    ]


    return (
        <div className="container-fluid py-5" id="team">
            <div className="container team text-center">
                <h2 className='text-center pt-2'>Our Team</h2>
                <div className="d-flex justify-content-center pb-3">
                    <div style={{ height: "3px", width: "8%", backgroundColor: "rgba(25,25,25,1)" }}></div>
                </div>
                <p className='pb-lg-5 pb-md-3 pb-2 team-description'>
                    Meet the brilliant minds behind our digital dreams - a team that blends
                    creativity with strategy to bring your vision to life. We are a band of
                    passionate individuals driven by the pursuit of excellence. With diverse
                    expertise and a shared zeal for innovation, we thrive on crafting compelling
                    narratives that resonate with your audience. Together, we form a powerhouse
                    of talents, united in our mission to elevate your online presence and fuel your
                    digital success. We're not just a team; we're your partners in growth,
                    dedicated to making your dreams a digital reality. Let's embark on this
                    transformative journey together!
                </p>
                <div id="carouselExampleInterval" className="carousel slide carousel-dark" data-bs-ride="carousel">
                    <div className="carousel-inner ">
                        {
                            teamDetails.map((item, index) => (
                                <div className={`carousel-item ${index === 0 && 'active'}`} key={index} data-bs-interval="3000">
                                    <div className="row justify-content-center">
                                        <div className="col-md-6 col-12 d-flex justify-content-center">
                                            <div className="card shadow" style={{ width: '18rem' }} >
                                                <img src={item.profileImg} className="card-img-top" alt="Profile" />
                                                <div className="card-body text-center border-top">
                                                    <h5 className="card-title">{item.name}</h5>
                                                    <p className="card-text">{item.Role}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    </div>

                    <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="prev">
                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Previous</span>
                    </button>
                    <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="next">
                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Next</span>
                    </button>
                </div>
            </div>
        </div>
    )
}

export default TeamComp