import React from 'react'

import infoImage from '../../assets/images/info3.png'
import './Home.css'

// components
import About from '../../Components/About/AboutComp'
import Service from '../../Components/Service/ServiceComp'
import Team from '../../Components/Team/TeamComp'
import Client from '../../Components/Client/ClientComp'
import Footer from '../../Components/Footer/Footer'



const Home = () => {
  return (
    <>
      <section className="container-fluid home">
        <div className="container">
          <div className="row justify-conten-center align-items-center m-md-0">
            <div className="col-lg-5 col-md-5 col-12 order-md-1 order-2">
              <h2 className="home-tag">Empower Your Online Success - Unleash the Power of WebPI.</h2>
              <h4 className="home-sub-tag">Elevate Your Brand, Captivate Customers, and Drive Sales with Our Expert Solutions.</h4>
            </div>
            <div className="col-lg-7 col-md-7 col-12 order-md-2 order-1 image-container">
              <img src={infoImage} alt="" className="img-fluid" />
            </div>
          </div>
        </div>
      </section>
      <About />
      <Service heading="Services" />
      <Team />
      <Client />
      <Footer />
    </>
  )
}

export default Home