import React from 'react'
import AboutImage from '../../assets/images/aboutus.jpg'
import './About.css'
const AboutComp = () => {
    return (
        <div className="container about py-2 py-md-4" id="aboutus">
            <h2 className="heading text-center pt-md-4 pt-2">About Us</h2>
            <div className="d-flex justify-content-center pb-2 pb-md-4">
                <div style={{ height: "3px", width: "8%", backgroundColor: "rgba(25,25,25,1)" }}></div>
            </div>
            <div className="row align-items-center justify-content-center">
                <div className="col-md-12 col-lg-5 col-12 about-img text-center">
                    <img src={AboutImage} alt="about" className="img-fluid " />
                </div>
                <div className="col-md-12 col-lg-7 col-12 about-info p-3">
                    <p >
                        Welcome to WebPI Consultant Services, your one-stop destination for
                        elevating your online presence and achieving remarkable success in the
                        digital realm. We are a team of passionate experts dedicated to providing
                        top-notch web development, digital marketing, social media marketing,
                        video editing, and graphic designing services. With a commitment to
                        excellence and a client-centric approach, we pride ourselves on being the
                        catalyst that drives your business towards unprecedented growth.
                    </p>
                </div>
            </div>
        </div>
    )
}

export default AboutComp